import Image from "next/image";
import React from "react";

function Calculator() {
  return (
    <div className="w-full max-w-[400px] md:max-w-full md:m-0 m-auto md:w-[50%] p-5 md:p-10">
      <div className="flex items-center justify-between">
        <h1 className="text-slate-900 font-bold text-[18px] md:text-[22px] lg:text-[25px]">
          Mortgage Calculator
        </h1>
        <button className="text-[14px] md:text-[17px] text-slate-700 underline underline-offset-2 cursor-pointer">
          Clear All
        </button>
      </div>
      <form className="mt-7">
        <label htmlFor="amount" className="text-slate-700 text-[14px] md:text-[17px]">Mortgage Amount</label>
        <div className="flex items-stretch rounded-md border-2 border-slate-700 mt-2">
          <div className="bg-slate-100 text-slate-700 text-[18px] md:text-[20px] font-bold py-2 px-4 rounded-l-md">Â£</div>
          <input className="w-full border-0 outline-0 text-[20px] font-bold px-4 no-spinner text-slate-900" type="number" id="amount" value={amount} onChange={handleAmount}  />
        </div>

        <div className="flex w-full gap-5 flex-col md:flex-row justify-between mt-6 items-center">
          <div className="w-full md:w-[50%]">
            <label className="text-slate-700 text-[14px] md:text-[17px]" htmlFor="term">Mortgage Term</label>
            <div className="flex items-stretch rounded-md border-2 border-slate-700 mt-2">
              <input className="w-full border-0 outline-0 text-[20px] font-bold px-4 no-spinner text-slate-900" type="number" id="term" value={term} onChange={handleTerm} />
              <div className="bg-slate-100 text-slate-700 text-[18px] md:text-[20px] font-bold py-2 px-4 rounded-r-md">years</div>
            </div>
          </div>
          <div className="w-full md:w-[50%]">
            <label className="text-slate-700 text-[14px] md:text-[17px]" htmlFor="rate">Interest Rate</label>
            <div className="flex items-stretch rounded-md border-2 border-slate-700 mt-2">
              <input className="w-full border-0 outline-0 text-[20px] font-bold px-4 no-spinner text-slate-900" type="number" id="rate" value={rate} onChange={handleRate} />
              <div className="bg-slate-100 text-slate-700 text-[18px] md:text-[20px] font-bold py-2 px-4 rounded-r-md">%</div>
            </div>
          </div>
        </div>

        <fieldset className="mt-6 w-full gap-3 flex flex-col">
          <legend className="text-slate-700 text-[14px] md:text-[18px] mb-2">Mortgage Type</legend>
          <label className="w-full font-bold text-[14px] md:text-[18px] border-2 border-slate-700 py-3 px-5 rounded-md flex items-center" htmlFor="repayment">
            <input className="mr-4 size-5" type="radio" id="repayment" /> Repayment
          </label>
          <label  className="w-full font-bold text-[14px] md:text-[18px] border-2 border-slate-700 py-3 px-5 rounded-md flex items-center" htmlFor="interest">
            <input className="mr-4 size-5" type="radio" id="interest" /> Interests Only
          </label>
        </fieldset>

        <button className="flex items-center justify-center rounded-4xl font-bold text-slate-900 p-4 text-[14px] md:text-[18px] bg-yellow-200 md:max-w-[350px] w-full mt-10" type="submit"> <Image className="w-6 mr-3" src='icon-calculator.svg' alt="" width={1000} height={1000} /> Calculate Repayments</button>
      </form>
    </div>
  );
}

export default Calculator;
